body{
  font-family: sans-serif, arial;
  color: #000000;
  background-color: #4A4F64;
  margin: 0px;
}
h1,h2,h3,h4,p,i,ul,ol,li,table,tr,td{
  margin: 0px;
  padding: 0px;
  border: 0px;
}
a{
  color: #4090E0;
  text-decoration: none;
}
ul{
  list-style-type: none;
}
textarea{
  resize: none;
  margin: 0px;
  padding: 5px;
  box-sizing: border-box;
}

/*.........................Common.........................*/
.window{
  background-color: #EFEFEF;
  width: 25cm;
  margin: auto;
}
.borderless-button{
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer and Edge */
  user-select: none; /* Chrome, Edge, Opera and Firefox */

  cursor: pointer;
  text-align: center;
  color: #0F0F0F;
}.borderless-button:hover{
  color: #3F3F3F;
}.borderless-button:active{
  color: #6F6F6F;
}
.borderless-button-extra{
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer and Edge */
  user-select: none; /* Chrome, Edge, Opera and Firefox */

  text-align: center;
  color: #0F0F0F40;
}.borderless-button-extra:hover{
  color: #3F3F3F;
}.borderless-button-extra:active{
  color: #6F6F6F;
}
.simple-button{
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer and Edge */
  user-select: none; /* Chrome, Edge, Opera and Firefox */

  cursor: pointer;
  text-align: center;
}.simple-button:hover{
  background-color: #DFDFDF;
}.simple-button:active{
  background-color: #BFBFBF;
}
.horizontally-aligned-listitem{
  float: left;
